a.navbar-brand {
  white-space: normal;
  text-align: center;
  word-break: break-all;
}

html {
  font-size: 14px;
}
@media (min-width: 768px) {
  html {
    font-size: 16px;
  }
}

.box-shadow {
  box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
}

.menu-bar {

  display: flex;
  align-items: center;
  border-radius: 0 !important;

}
.menu-bar span {
  color: #fff;
  padding: 0 20px;
}
